@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap);
body,.App{height:100vh;width:100%;margin:0px;background:linear-gradient(to top, #7A5B6E, #423551);color:white;font-family:'Montserrat';-webkit-user-select:none;-ms-user-select:none;user-select:none;overflow:hidden}.App{display:flex;flex-direction:column;justify-content:center;align-items:center}.App .slider{display:flex;flex-direction:row;flex-basis:auto;max-width:100%;padding:20px 0px;overflow-x:auto;-webkit-overflow-scrolling:touch;scroll-behavior:smooth}.App .slider::-webkit-scrollbar{height:8px;border-radius:10px;background-color:rgba(255,255,255,0)}.App .slider::-webkit-scrollbar-thumb{background-color:rgba(255,255,255,0.699);border-radius:10px}.App .footer{zoom:1.2;position:absolute;bottom:2px;font-size:15px;font-weight:300;margin:0px;animation:fadeIn ease-in 1s forwards}.App .footer img{width:15px;vertical-align:middle}.App .footer a{color:#E57873;text-decoration:none}@media screen and (max-height: 800px){.footer{display:none}}.alert{display:flex;flex-direction:row;align-items:center;justify-content:center;background-color:rgba(255,177,177,0.116);border-radius:4px;height:40px;opacity:0%}.alert img{width:24px;height:24px;padding:0px 10px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.alert img:last-of-type{transition:all 0.5s ease;opacity:60%}.alert img:last-of-type:hover{opacity:100%}.alert p{font-size:12px;font-weight:300;margin:0px}.fade-in{animation:fadeIn ease-in 1s forwards}.fade-in .img:last-of-type{cursor:pointer}.fade-out{animation:fadeOut ease-in 1s forwards}@keyframes fadeIn{from{opacity:0%}to{opacity:100%}}@keyframes fadeOut{from{opacity:100%}to{opacity:0%}}.flex-col{display:flex;flex-direction:column;align-items:center;justify-content:center}.day{background:linear-gradient(45deg, #E3E3C3, #AED2C6, #58B8CC)}.day .sun{width:50px;height:50px;background-color:#FFEFCC;box-shadow:0 0 50px 0px #fff, 0 0 100px 0px #ff0}.day #mount{fill:#D29558}.day .footer-content{background-color:#D29558}.night{background:linear-gradient(to top, #846279, #E29BAA, #846279)}.night .sun{width:50px;height:50px;background:#FFFDF4;box-shadow:0 0 50px 0px #fff, 0 0 100px 0px #ff0}.night #mount{fill:#4F425D}.night .footer-content{background-color:#4F425D}.fullmoon{background:linear-gradient(to top, #200E3B, #313366, #200E3B)}.fullmoon .sun{width:65px;height:65px;background:#E57873;box-shadow:0 0 80px 0px #fff, 0 0 200px 0px #f0f}.fullmoon #mount{fill:#31253B}.fullmoon .footer-content{background-color:#31253B}.litecloudy .clouds{position:absolute;top:0px;z-index:1;width:100%;height:150px}.litecloudy .clouds .cloud{opacity:60%;position:absolute;width:100%;height:150px}.litecloudy .clouds .cloud1{background:center no-repeat url(data:image/png;base64,);animation:animatedBackground 120s linear infinite}.litecloudy .clouds .cloud2{background:center no-repeat url(/static/media/cloud2.e63e6ca1.png);animation:animatedBackground 80s linear infinite}.litecloudy .clouds .cloud3{background:center no-repeat url(data:image/png;base64,);animation:animatedBackground 140s linear infinite}.overcast .clouds{position:absolute;top:0px;z-index:1;width:100%;height:150px}.overcast .clouds .cloud{position:absolute;width:100%;height:150px;background-size:contain !important}.overcast .clouds .cloud1{background:center no-repeat url(data:image/png;base64,);animation:animatedBackground 80s linear infinite}.overcast .clouds .cloud2{background:center no-repeat url(/static/media/cloud2.e63e6ca1.png);animation:animatedBackground 100s linear infinite}.overcast .clouds .cloud3{background:center no-repeat url(data:image/png;base64,);animation:animatedBackground 120s linear infinite}.overcast .clouds .cloud4{background:center no-repeat url(/static/media/cloud4.743586a7.png);animation:animatedBackground 140s linear infinite}.overcast .clouds .cloud5{background:center no-repeat url(/static/media/cloud5.f0102289.png);animation:animatedBackground 160s linear infinite}.overcast .clouds .cloud6{background:center no-repeat url(/static/media/cloud6.4f4c783d.png);animation:animatedBackground 180s linear infinite}.overcast .clouds .cloud7{background:center no-repeat url(/static/media/cloud7.9131c3bd.png);animation:animatedBackground 200s linear infinite}@keyframes animatedBackground{from{transform:translate(-400px, 0px)}to{transform:translate(400px, 0px)}}

.Weather{display:flex;flex-direction:row;align-items:center;justify-content:center;margin:25px 15px;animation:fadeIn ease-in 1s forwards;border-radius:10px;zoom:1.2}.Weather .card{width:400px;display:flex;flex-direction:column;align-items:center;justify-content:space-between;box-shadow:rgba(0,0,0,0.25) 0px 14px 28px,rgba(0,0,0,0.22) 0px 10px 10px;border-radius:10px;overflow:hidden;animation:fadeIn ease-in 1s forwards}.Weather .card .top{display:flex;flex-direction:column;align-items:center;width:100%}.Weather .card .top .close-component{width:32px;margin-bottom:25px;margin-left:auto;margin-right:5px;margin-top:5px;cursor:pointer;opacity:0.5;transition:all 0.5s ease;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Weather .card .top .close-component:hover{opacity:1}.Weather .card .top h1{font-size:60px;font-weight:300;margin:0px}.Weather .card .top h2{font-weight:300;margin:0px;text-transform:capitalize}.Weather .card .top h3{font-weight:400;margin:0px}.Weather .card .top p{font-size:12px;font-weight:300;padding-top:2px;margin:0px;margin-bottom:10px;opacity:0.6}.Weather .card .middle{display:flex;position:relative;width:100%;flex-direction:row;align-content:center;justify-content:center;align-items:center}.Weather .card .middle .sun-viewbox{display:flex;flex-direction:row;justify-content:center;align-items:center;width:75px;height:75px}.Weather .card .middle .sun-viewbox .sun{bottom:0px;z-index:0;margin-bottom:0px;border-radius:50%}.Weather .card .bot{width:100%;height:100%;display:flex;flex-direction:column;align-items:center}.Weather .card .bot svg{display:flex;flex-direction:column;align-items:center;width:110%;z-index:2}.Weather .card .bot svg .cls-1{fill:none}.Weather .card .bot svg .cls-2{fill:url(#Dégradé_sans_nom_13)}.Weather .card .bot svg .cls-3{fill:url(#Dégradé_sans_nom_13-2)}.Weather .card .bot svg .cls-4{fill:url(#Dégradé_sans_nom_13-3)}.Weather .card .bot svg .cls-5{fill:url(#Dégradé_sans_nom_13-4)}.Weather .card .bot svg .cls-6{fill:url(#Dégradé_sans_nom_13-5)}.Weather .card .bot .footer-content{display:flex;width:90%;height:150px;align-content:center;flex-direction:column;padding:0 5% 2% 5%;z-index:2}.Weather .card .bot .footer-content .top{display:flex;flex-direction:row;justify-content:space-between;align-content:space-between}.Weather .card .bot .footer-content .top h3{font-size:15px;font-weight:400;font-family:'Montserrat';background-color:rgba(0,0,0,0);color:white;border:none;text-transform:capitalize;margin:0px 10px}.Weather .card .bot .footer-content .top div{display:flex;flex-direction:row}.Weather .card .bot .footer-content .top div svg{cursor:pointer;margin:0px 4px;opacity:0.5;transition:all 0.5s ease}.Weather .card .bot .footer-content .top div svg:hover{opacity:1}.Weather .card .bot .footer-content .top div .active-svg{opacity:1}.Weather .card .bot .footer-content .line{margin:5px 0px;border-top:1px solid #d3d3d3;width:100%}.Weather .card .bot .footer-content .bot .hide{display:none;transition:all 0.5s ease}.Weather .card .bot .footer-content .bot .tab-temp{width:100%;display:flex;flex-direction:row;justify-content:space-between;overflow:scroll;transition:all 0.5s ease}.Weather .card .bot .footer-content .bot .tab-temp::-webkit-scrollbar{display:none}.Weather .card .bot .footer-content .bot .tab-stat{width:100%}.Weather .card .bot .footer-content .bot .tab-stat::-webkit-scrollbar{display:none}.Weather .card .bot .footer-content .bot .tab-settings{width:100%}.Weather .card .bot .footer-content .bot .tab-settings::-webkit-scrollbar{display:none}@keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn50{from{opacity:0.5}to{opacity:1}}@media screen and (max-width: 425px) and (orientation: portrait){.card{width:100vw !important}}

.weather-wrap{display:flex;flex-direction:column;margin:0px 10px;align-items:center}.weather-wrap h3{font-size:15px;font-weight:500;margin:10px 0px}.weather-wrap img{width:35px;height:35px;-webkit-user-select:none;-ms-user-select:none;user-select:none}

.average-wrap{display:flex;flex-direction:column;margin:0px 10px;align-items:center;justify-content:space-between;transition:all 0.5s ease}.average-wrap .col{width:100%}.average-wrap .col ul{display:flex;flex-direction:row;margin:0px;padding:0px;list-style:none;justify-content:space-between}.average-wrap .col ul li h3{font-weight:300;font-size:10px;opacity:0.6;margin:0px}.average-wrap .col ul li p{font-weight:300;font-size:16px;margin:0px}.average-wrap .line{opacity:0.4;margin:3px 0px !important}

.settings-wrap{display:flex;flex-direction:row;margin:10px;align-items:center;justify-content:space-between}.settings-wrap .right{text-align:end}.settings-wrap ul{list-style:none;margin:0px;padding:0px}.settings-wrap ul li{font-weight:400;font-size:15px}.settings-wrap ul li img{display:inline;width:20px;vertical-align:middle;cursor:pointer;transition:all 0.5s ease;opacity:0.5}.settings-wrap ul li span{font-weight:300;font-size:15px;opacity:0.5;cursor:pointer;transition:all 0.5s ease}.settings-wrap ul li span a{color:white}.settings-wrap ul li .active{opacity:1}

.Loader{display:flex;flex-direction:column;align-items:center;zoom:1.2}.Loader h1{font-weight:300;font-size:18px;margin:0px}.Loader h1 span:first-of-type{animation:.4s ease-in 0s infinite alternate fadeIn}.Loader h1 span{animation:.4s ease-in .2s infinite alternate fadeIn}.Loader h1 span:last-of-type{animation:.4s ease-in .4s infinite alternate fadeIn}.Loader img{margin-bottom:20px;width:80px}@keyframes fadeIn{0%{opacity:0}50%{opacity:1}100%{opacity:1}}

.wrapper-input{display:flex;flex-direction:column;align-items:center;justify-content:center;min-width:400px;zoom:1.2}.wrapper-input form{display:flex;flex-direction:column;align-items:center;justify-content:center;margin:20px 0px}.wrapper-input form label{margin:2px;font-size:20px;font-weight:300;font-family:'Montserrat'}.wrapper-input form input{font-size:15px;font-weight:300;text-align:center;font-family:'Montserrat';background-color:rgba(255,255,255,0);color:white;border:none;padding:10px;border-bottom:2px solid lightgrey;border-radius:0}.wrapper-input form input:focus{outline:none;box-shadow:none}

